import * as XLSX from 'xlsx';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { Enterprise, IndustryStats, RegionStats } from '../types/enterprise';

// –ò–Ω—Ç–µ—Ä—Ñ–µ–π—Å –¥–ª—è –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ –æ—Ç—á–µ—Ç–∞
export interface ReportData {
  title: string;
  period: {
    from: string;
    to: string;
  };
  enterprises: Enterprise[];
  stats: {
    totalEnterprises: number;
    totalRevenue: number;
    totalEmployees: number;
    averageRevenue: number;
    averageEmployees: number;
  };
  industryStats?: IndustryStats[];
  regionStats?: RegionStats[];
}

// –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –≤–∞–ª—é—Ç—ã –¥–ª—è –æ—Ç—á–µ—Ç–æ–≤
const formatCurrencyForExport = (value: number): string => {
  return new Intl.NumberFormat('ru-RU', {
    style: 'currency',
    currency: 'RUB',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(value);
};

// –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —á–∏—Å–µ–ª
const formatNumberForExport = (value: number): string => {
  return new Intl.NumberFormat('ru-RU').format(value);
};

// –ì–µ–Ω–µ—Ä–∞—Ü–∏—è Excel –æ—Ç—á–µ—Ç–∞
export const generateExcelReport = (reportData: ReportData) => {
  const workbook = XLSX.utils.book_new();

  // –õ–∏—Å—Ç 1: –û–±—â–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è
  const summaryData = [
    ['–û—Ç—á—ë—Ç –ø–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è–º –ú–æ—Å–∫–≤—ã'],
    ['–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ç—á—ë—Ç–∞:', reportData.title],
    ['–î–∞—Ç–∞ —Å–æ–∑–¥–∞–Ω–∏—è:', new Date().toLocaleString('ru-RU')],
    ['–ü–µ—Ä–∏–æ–¥:', `${reportData.period.from} - ${reportData.period.to}`],
    [],
    ['–û–±—â–∞—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞'],
    ['–í—Å–µ–≥–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π:', reportData.stats.totalEnterprises],
    ['–û–±—â–∞—è –≤—ã—Ä—É—á–∫–∞:', formatCurrencyForExport(reportData.stats.totalRevenue)],
    ['–í—Å–µ–≥–æ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–æ–≤:', formatNumberForExport(reportData.stats.totalEmployees)],
    ['–°—Ä–µ–¥–Ω—è—è –≤—ã—Ä—É—á–∫–∞:', formatCurrencyForExport(reportData.stats.averageRevenue)],
    ['–°—Ä–µ–¥–Ω—è—è —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å:', formatNumberForExport(Math.round(reportData.stats.averageEmployees))],
  ];
  
  const summarySheet = XLSX.utils.aoa_to_sheet(summaryData);
  XLSX.utils.book_append_sheet(workbook, summarySheet, '–û–±—â–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è');

  // –õ–∏—Å—Ç 2: –ü—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è
  if (reportData.enterprises.length > 0) {
    const enterprisesData = reportData.enterprises.map(e => ({
      '–ù–∞–∑–≤–∞–Ω–∏–µ': e.name,
      '–û—Ç—Ä–∞—Å–ª—å': e.industry,
      '–†–µ–≥–∏–æ–Ω': e.region,
      '–°–æ—Ç—Ä—É–¥–Ω–∏–∫–∏': e.employees,
      '–í—ã—Ä—É—á–∫–∞ (—Ä—É–±.)': e.revenue,
      '–ù–∞–ª–æ–≥–∏ (—Ä—É–±.)': e.taxesPaid,
      '–°—Ç–∞—Ç—É—Å': e.status === 'active' ? '–ê–∫—Ç–∏–≤–Ω–æ' : e.status === 'inactive' ? '–ù–µ–∞–∫—Ç–∏–≤–Ω–æ' : '–ü—Ä–∏–æ—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–æ',
      '–ê–¥—Ä–µ—Å': e.contactInfo.address,
      '–¢–µ–ª–µ—Ñ–æ–Ω': e.contactInfo.phone || '-',
      'Email': e.contactInfo.email || '-',
    }));

    const enterprisesSheet = XLSX.utils.json_to_sheet(enterprisesData);
    XLSX.utils.book_append_sheet(workbook, enterprisesSheet, '–ü—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è');
  }

  // –õ–∏—Å—Ç 3: –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ –æ—Ç—Ä–∞—Å–ª—è–º
  if (reportData.industryStats && reportData.industryStats.length > 0) {
    const industryData = reportData.industryStats.map(i => ({
      '–û—Ç—Ä–∞—Å–ª—å': i.industry,
      '–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π': i.count,
      '–û–±—â–∞—è –≤—ã—Ä—É—á–∫–∞ (—Ä—É–±.)': i.totalRevenue,
      '–°—Ä–µ–¥–Ω—è—è —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å': i.averageEmployees,
    }));

    const industrySheet = XLSX.utils.json_to_sheet(industryData);
    XLSX.utils.book_append_sheet(workbook, industrySheet, '–ü–æ –æ—Ç—Ä–∞—Å–ª—è–º');
  }

  // –õ–∏—Å—Ç 4: –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ —Ä–µ–≥–∏–æ–Ω–∞–º
  if (reportData.regionStats && reportData.regionStats.length > 0) {
    const regionData = reportData.regionStats.map(r => ({
      '–†–µ–≥–∏–æ–Ω': r.region,
      '–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π': r.count,
      '–û–±—â–∞—è –≤—ã—Ä—É—á–∫–∞ (—Ä—É–±.)': r.totalRevenue,
      '–°—Ä–µ–¥–Ω—è—è —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å': r.averageEmployees,
    }));

    const regionSheet = XLSX.utils.json_to_sheet(regionData);
    XLSX.utils.book_append_sheet(workbook, regionSheet, '–ü–æ —Ä–µ–≥–∏–æ–Ω–∞–º');
  }

  // –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ —Ñ–∞–π–ª–∞
  const fileName = `report_${Date.now()}.xlsx`;
  XLSX.writeFile(workbook, fileName);
};

// –ì–µ–Ω–µ—Ä–∞—Ü–∏—è PDF –æ—Ç—á–µ—Ç–∞
export const generatePDFReport = (reportData: ReportData) => {
  const doc = new jsPDF();

  // –ò—Å–ø–æ–ª—å–∑—É–µ–º –±–∞–∑–æ–≤—ã–π —à—Ä–∏—Ñ—Ç —Å –ø–æ–¥–¥–µ—Ä–∂–∫–æ–π —é–Ω–∏–∫–æ–¥–∞
  doc.setFont('helvetica');

  // –ó–∞–≥–æ–ª–æ–≤–æ–∫
  doc.setFontSize(18);
  doc.text('–û—Ç—á—ë—Ç –ø–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è–º –ú–æ—Å–∫–≤—ã', 14, 15);

  doc.setFontSize(12);
  doc.text(`–ù–∞–∑–≤–∞–Ω–∏–µ: ${reportData.title}`, 14, 25);
  doc.text(`–î–∞—Ç–∞ —Å–æ–∑–¥–∞–Ω–∏—è: ${new Date().toLocaleDateString('ru-RU')}`, 14, 32);
  doc.text(`–ü–µ—Ä–∏–æ–¥: ${reportData.period.from} - ${reportData.period.to}`, 14, 39);

  // –û–±—â–∞—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞
  doc.setFontSize(14);
  doc.text('–û–±—â–∞—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞', 14, 50);

  doc.setFontSize(11);
  doc.text(`–í—Å–µ–≥–æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π: ${reportData.stats.totalEnterprises}`, 14, 58);
  doc.text(`–û–±—â–∞—è –≤—ã—Ä—É—á–∫–∞: ${formatCurrencyForExport(reportData.stats.totalRevenue)}`, 14, 65);
  doc.text(`–í—Å–µ–≥–æ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–æ–≤: ${formatNumberForExport(reportData.stats.totalEmployees)}`, 14, 72);
  doc.text(`–°—Ä–µ–¥–Ω—è—è –≤—ã—Ä—É—á–∫–∞: ${formatCurrencyForExport(reportData.stats.averageRevenue)}`, 14, 79);
  doc.text(`–°—Ä–µ–¥–Ω—è—è —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å: ${formatNumberForExport(Math.round(reportData.stats.averageEmployees))}`, 14, 86);

  let yPos = 95;

  // –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ –æ—Ç—Ä–∞—Å–ª—è–º
  if (reportData.industryStats && reportData.industryStats.length > 0) {
    doc.setFontSize(14);
    doc.text('–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ –æ—Ç—Ä–∞—Å–ª—è–º', 14, yPos);
    yPos += 10;

    const industryTableData = reportData.industryStats.map(i => [
      i.industry,
      i.count.toString(),
      formatCurrencyForExport(i.totalRevenue),
      i.averageEmployees.toString(),
    ]);

    autoTable(doc, {
      startY: yPos,
      head: [['–û—Ç—Ä–∞—Å–ª—å', '–ö–æ–ª-–≤–æ', '–í—ã—Ä—É—á–∫–∞', '–°—Ä–µ–¥. —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å']],
      body: industryTableData,
      theme: 'grid',
      styles: {
        fontSize: 9,
        font: 'helvetica'
      },
      headStyles: {
        fillColor: [59, 130, 246],
        font: 'helvetica',
        fontStyle: 'bold'
      },
    });

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    yPos = (doc as any).lastAutoTable.finalY + 10;
  }

  // –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ —Ä–µ–≥–∏–æ–Ω–∞–º
  if (reportData.regionStats && reportData.regionStats.length > 0) {
    if (yPos > 250) {
      doc.addPage();
      yPos = 15;
    }

    doc.setFontSize(14);
    doc.text('–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ —Ä–µ–≥–∏–æ–Ω–∞–º', 14, yPos);
    yPos += 10;

    const regionTableData = reportData.regionStats.map(r => [
      r.region,
      r.count.toString(),
      formatCurrencyForExport(r.totalRevenue),
      r.averageEmployees.toString(),
    ]);

    autoTable(doc, {
      startY: yPos,
      head: [['–†–µ–≥–∏–æ–Ω', '–ö–æ–ª-–≤–æ', '–í—ã—Ä—É—á–∫–∞', '–°—Ä–µ–¥. —á–∏—Å–ª–µ–Ω–Ω–æ—Å—Ç—å']],
      body: regionTableData,
      theme: 'grid',
      styles: {
        fontSize: 9,
        font: 'helvetica'
      },
      headStyles: {
        fillColor: [16, 185, 129],
        font: 'helvetica',
        fontStyle: 'bold'
      },
    });

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    yPos = (doc as any).lastAutoTable.finalY + 10;
  }

  // –¢–æ–ø 10 –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π
  if (reportData.enterprises.length > 0) {
    if (yPos > 220) {
      doc.addPage();
      yPos = 15;
    }

    doc.setFontSize(14);
    doc.text('–¢–æ–ø 10 –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π –ø–æ –≤—ã—Ä—É—á–∫–µ', 14, yPos);
    yPos += 10;

    const topEnterprises = [...reportData.enterprises]
      .sort((a, b) => b.revenue - a.revenue)
      .slice(0, 10);

    const enterprisesTableData = topEnterprises.map(e => [
      e.name,
      e.industry,
      e.employees.toString(),
      formatCurrencyForExport(e.revenue),
    ]);

    autoTable(doc, {
      startY: yPos,
      head: [['–ù–∞–∑–≤–∞–Ω–∏–µ', '–û—Ç—Ä–∞—Å–ª—å', '–°–æ—Ç—Ä—É–¥–Ω–∏–∫–∏', '–í—ã—Ä—É—á–∫–∞']],
      body: enterprisesTableData,
      theme: 'grid',
      styles: {
        fontSize: 8,
        font: 'helvetica'
      },
      headStyles: {
        fillColor: [139, 92, 246],
        font: 'helvetica',
        fontStyle: 'bold'
      },
    });
  }

  // –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ —Ñ–∞–π–ª–∞
  const fileName = `otchet_${Date.now()}.pdf`;
  doc.save(fileName);
};
